let canvas = document.getElementById("canvas") as HTMLCanvasElement;
let ctx = canvas.getContext("2d");

let x = 0;
let y = 0;

function update() {
  x += 10;
  y += 10;
  if (x >= 800) {
    x = 0;
  }
  if (x < 0) {
    x = 800;
  }
  if (y >= 600) {
    y = 0;
  }
  if (y < 0) {
    y = 600;
  }
}

function draw() {
  ctx.clearRect(0, 0, 800, 600);
  ctx.fillStyle = "white";
  ctx.fillRect(x, y, 10, 10);
}

function loop() {
  update();
  draw();
  window.requestAnimationFrame(loop);
}

loop();
